// import { MH_API } from "@/api/api";
import orderItem from "./orderItem";
export default {
  name: "orderRight",
  components: {
    orderItem
  },
  props: {},
  data() {
    return {
      orderList: [],
      pages: 1,
      per_page: 5,
      loadShow: false,
      isNowState: false, //判断是否正在请求数据当中
      isDataState: false, //数据是否已请求完毕
      isScroll: true, //判断是否已经触发函数
      types: "all" //all
    };
  },
  mounted() {
    let thif = this;
    window.onscroll = function() {
      if (thif.isScroll && !thif.isNowState && !thif.isDataState) {
        if (
          thif.common.getScrollTop() + thif.common.getClientHeight() >=
          thif.common.getScrollHeight()
        ) {
          //写后台加载数据的函数
          // console.log("距顶部"+scrollTop+"可视区高度"+windowHeight+"滚动条总高度"+scrollHeight);
          thif.isScroll = false;
          thif.getOrderList();
        }
      }
    };
    this.getOrderList();
  },
  methods: {
    getOrderList() {
      let thif = this;
      thif.loadShow = true;
      thif.isNowState = true;
      this.$apis
        .orderList(
          {
            page: this.pages,
            per_page: this.per_page,
            type: this.types
          },
          this.$store.getters.getApiHeader
        )
        .then(res => {
          console.log(res);
          thif.isScroll = true;
          thif.isNowState = false;
          thif.loadShow = false;
          if (res.status == "1") {
            thif.pages++;
            thif.orderList.push(...res.data.items);
            if (res.data.items.length == 0) {
              thif.isDataState = true;
            }
          } else {
            thif.common.msgBox(thif, res.msg, 3);
          }
        })
        .catch(err => {
          thif.isScroll = true;
          thif.isNowState = false;
          thif.loadShow = false;
          console.log(err);
        });
    },
    cutList(type) {
      this.types = type;
      this.pages = 1;
      this.isDataState = false;
      this.orderList = [];
      this.getOrderList();
    }
  }
};
